.topThumbCard {
  float: left;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  width: 23.5% !important;
  margin-left: 1.5%;
  margin-bottom: 10px;
  box-sizing: border-box;
  border: 1px solid #E9ECFA !important; }
  .topThumbCard .pull-right {
    float: right; }
  .topThumbCard .card-title {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    margin-top: 10px;
    font-size: 14px !important;
    color: #444;
    font-weight: bold;
    height: 42px;
    overflow: hidden;
    text-overflow: ellipsis; }
    .topThumbCard .card-title .rating {
      flex-shrink: 0;
      color: #5931ea;
      font-size: 10px; }
  .topThumbCard .userinfo {
    display: flex;
    align-items: center;
    justify-content: flex-start; }
    .topThumbCard .userinfo .pic {
      width: 32px;
      height: 32px;
      background-size: cover;
      background-repeat: no-repeat;
      background-position: center center;
      margin-right: 10px; }
    .topThumbCard .userinfo .fa-star {
      color: #5931ea; }
  .topThumbCard .body {
    width: 100%;
    padding: 10px;
    height: 152px; }
  .topThumbCard .dots {
    width: 3px !important; }
  .topThumbCard .block {
    display: block;
    width: 100%; }
  .topThumbCard .thumb {
    border: 10px solid #fff;
    width: 100%;
    height: 100px;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover; }
  .topThumbCard .highlight-title {
    margin-top: 0;
    font-weight: normal;
    font-size: 14px;
    font-weight: bold;
    height: 40px;
    overflow: hidden; }
  .topThumbCard .highlight-price {
    font-weight: bolder; }
  .topThumbCard .highlight-category {
    font-weight: normal;
    color: #5931ea; }
  .topThumbCard .highlight-userinfo {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding: 10px 0 5px; }
  .topThumbCard .highlight-profile-pic {
    width: 20px;
    height: 20px;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    border-radius: 50%;
    margin-right: 10px; }
  .topThumbCard .highlight-desc {
    width: calc(100% - 100px);
    padding: 10px;
    font-size: 14px;
    position: relative;
    height: 100px;
    box-sizing: border-box; }
    .topThumbCard .highlight-desc p {
      margin-bottom: 0; }
  .topThumbCard .highlight-meta {
    display: flex;
    justify-content: center;
    font-weight: bold;
    border-top: 1px solid #E9ECFA;
    /* padding-top: 4px; */
    position: absolute;
    width: 90%;
    /* bottom: 0; */
    padding: 15px 0;
    font-size: 0.7rem; }
  .topThumbCard .highlight-price, .topThumbCard .price {
    color: #3C49F1;
    font-size: 0.7rem;
    margin-left: 5px; }
  @media screen and (max-width: 1150px) {
    .topThumbCard {
      width: 48.5% !important; } }
  @media only screen and (max-width: 600px) {
    .topThumbCard {
      width: 98.5% !important; } }
  .topThumbCard .waiting {
    background-color: #5931ea;
    color: #fff;
    padding: 5px;
    margin: 0 auto;
    display: block;
    text-align: center; }
