.SocialMenus {
  border-radius: 6px; }
  .SocialMenus .menu li {
    padding: 0.5rem 0.5rem;
    border-radius: 0;
    font-size: 0.8rem; }
    .SocialMenus .menu li:hover {
      font-weight: bold; }
    .SocialMenus .menu li.current {
      color: #5931ea !important;
      border-radius: 20px;
      font-weight: bold; }

.searchMenu {
  list-style: none;
  margin: 0;
  padding: 0; }
  .searchMenu li {
    display: block; }
    .searchMenu li .pointer {
      display: block;
      padding: 4px 10px;
      background-color: #ffffff;
      border: 1px solid #d2d2d2;
      margin-bottom: 4px; }
      .searchMenu li .pointer.active {
        background: #5931ea;
        color: #fff;
        border-color: #5931ea; }
      .searchMenu li .pointer:hover {
        background: #121a50;
        color: #fff;
        border-color: #121a50; }

.gigsCategoryMenu {
  flex-direction: column;
  padding: 0;
  border: 1px solid #ddd !important; }
  .gigsCategoryMenu .design-text.active {
    color: #5931ea; }
  .gigsCategoryMenu ul {
    padding: 0; }

.gigonomyMenus .submenu {
  margin-left: 40px !important;
  background: #dfe4ff;
  border-radius: 20px !important; }
  .gigonomyMenus .submenu.current {
    background: #5931ea;
    color: #fff; }
