.modal {
  background-color: black;
  /* Fallback color */
  background-color: rgba(0, 0, 0, 0.4);
  /* Overlay effect: translucent background: black w/ partial opacity */
  z-index: 999;
  /* Overlay effect: positioned over other containers */
  width: 100%;
  /* Full width */
  height: 100%;
  /* Full height */
  position: fixed;
  /* Fix position on the top-left corner*/
  top: 0;
  left: 0;
  overflow: auto;
  /* Enable scroll if needed */
  padding-top: 80px;
  /* Location of the content container */
  /* Modal content */
  /* Close button */ }
  .modal .modal-content {
    background-color: white;
    width: max-content;
    min-width: 250px;
    max-width: 800px;
    height: auto;
    min-height: 100px;
    margin: auto;
    padding: 10px;
    box-shadow: 0 0 10px #0000006b;
    border-radius: 6px; }
    .modal .modal-content.big {
      max-width: 1000px; }
  .modal .close {
    color: #ffe4e4;
    float: right;
    font-size: 18px !important;
    font-weight: bold;
    position: absolute;
    right: -7px;
    background: #ff5c5c;
    width: 20px;
    height: 20px;
    display: flex;
    align-items: center;
    opacity: 1;
    justify-content: center;
    border-radius: 50%;
    top: -6px; }
  .modal .close:hover,
  .modal .close:focus {
    color: #000;
    text-decoration: none;
    cursor: pointer; }
